<template>
  <div class="auth-wrapper auth-v2">
    <b-row class="auth-inner m-0">

      <!-- Brand logo-->
      <!--<b-link class="brand-logo">
        <vuexy-logo />
        <h2 class="brand-text text-primary ml-1">
          Vuexy
        </h2>
      </b-link>-->
      <!-- /Brand logo-->

      <!-- Left Text-->
      <b-col
        lg="8"
        class="d-none d-lg-flex align-items-center p-5"
      >
        <div class="w-100 d-lg-flex align-items-center justify-content-center px-5">
          <b-img
            fluid
            :src="imgUrl"
            alt="Login V2"
          />
        </div>
      </b-col>
      <!-- /Left Text-->

      <!-- Login-->
      <b-col
        lg="4"
        class="d-flex align-items-center auth-bg px-2 p-lg-5"
      >
        <b-col
          sm="8"
          md="6"
          lg="12"
          class="px-xl-2 mx-auto"
        >
          <b-card-title
            title-tag="h2"
            class="font-weight-bold mb-1"
          >
            Ticket Trace System
          </b-card-title>
          <b-card-text class="mb-2">
            Please sign-in to your account and start the adventure
          </b-card-text>

          <!-- form -->
          <validation-observer ref="loginValidation">
            <b-form
              class="auth-login-form mt-2"
              @submit.prevent
            >
              <!-- email -->
              <b-form-group
                label="Email"
                label-for="login-email"
              >
                <validation-provider
                  #default="{ errors }"
                  name="Email"
                  rules="required|email"
                >
                  <b-form-input
                    id="login-email"
                    v-model="formdata.Email"
                    :state="errors.length > 0 ? false:null"
                    name="login-email"
                    placeholder="john@example.com"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>

              <!-- forgot password -->
              <b-form-group>
                <div class="d-flex justify-content-between">
                  <label for="login-password">Password</label>
                </div>
                <validation-provider
                  #default="{ errors }"
                  name="Password"
                  rules="required"
                >
                  <b-input-group
                    class="input-group-merge"
                    :class="errors.length > 0 ? 'is-invalid':null"
                  >
                    <b-form-input
                      id="login-password"
                      v-model="formdata.Password"
                      :state="errors.length > 0 ? false:null"
                      class="form-control-merge"
                      :type="passwordFieldType"
                      name="login-password"
                      placeholder=""
                    />
                    <b-input-group-append is-text>
                      <feather-icon
                        class="cursor-pointer"
                        :icon="passwordToggleIcon"
                        @click="togglePasswordVisibility"
                      />
                    </b-input-group-append>
                  </b-input-group>
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>

              <!-- checkbox -->
              <b-form-group>
                <b-form-checkbox
                  id="remember-me"
                  v-model="status"
                  name="checkbox-1"
                >
                  Remember Me
                </b-form-checkbox>
              </b-form-group>

              <!-- submit buttons -->
              <b-button
                type="submit"
                variant="primary"
                block
                @click="login"
              >
                Sign in
              </b-button>
            </b-form>
          </validation-observer>



        </b-col>
      </b-col>
    <!-- /Login-->
    </b-row>
  </div>
</template>

<script>
    /* eslint-disable global-require */
    import { defineComponent, computed, ref } from 'vue';
import { ValidationProvider, ValidationObserver } from 'vee-validate'
import VuexyLogo from '@core/layouts/components/Logo.vue'
import {
  BRow, BCol, BLink, BFormGroup, BFormInput, BInputGroupAppend, BInputGroup, BFormCheckbox, BCardText, BCardTitle, BImg, BForm, BButton,
} from 'bootstrap-vue'
import { required, email } from '@validations'
import { togglePasswordVisibility } from '@core/mixins/ui/forms'
//import store from '@/store/index'
    import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
    import service from '../auth/request';
    import navMenuItems from '@/navigation/vertical'
    import jwt_decode from "jwt-decode";
export default {
  components: {
    BRow,
    BCol,
    BLink,
    BFormGroup,
    BFormInput,
    BInputGroupAppend,
    BInputGroup,
    BFormCheckbox,
    BCardText,
    BCardTitle,
    BImg,
    BForm,
    BButton,
    VuexyLogo,
    ValidationProvider,
    ValidationObserver,
  },
  mixins: [togglePasswordVisibility],
  data() {
      return {
          formdata: {
              Password: '',
              Email: '',
          },
        status: '',
        sideImg: require('@/assets/images/pages/login-v2.svg'),
        // validation rulesimport store from '@/store/index'
        required,
          email,
    }
  },
  computed: {
    passwordToggleIcon() {
      return this.passwordFieldType === 'password' ? 'EyeIcon' : 'EyeOffIcon'
    },
    imgUrl() {
        if (this.$store.state.appConfig.layout.skin === 'dark') {
        // eslint-disable-next-line vue/no-side-effects-in-computed-properties
        this.sideImg = require('@/assets/images/pages/login-v2-dark.svg')
        return this.sideImg
      }
      return this.sideImg
    },
        },

        methods: {
            login() {
                this.Login(this.formdata, this.$store)
                    .then(() => {
                        //errorMessages.value = []
                        this.$router.push({
                            path: "/menu",
                            name: "menu",
                        }).catch(e => {
                            console.log(e)
                        });
                    })
                    .catch(error => {
                        /*if (error.response.status == 400) {
                            errorMessages.value = error.response.data["Email"]
                        }*/
                        console.log(error.response)
                    })
            },

            GetUserInfo(store) {
                return service.get('/api/Account/GetUserInfo')
                    .then(res => {
                        store.state.claims = res.data.claims;
                        console.log(store.state.claims);

                        
                        //axios.get('http://localhost:8088/api/Roles/1')  //this.$store.state.claims.role)
                        //    .then(res => {
                        //        navMenuItems.splice(0)
                        //        Array.prototype.push.apply(navMenuItems, JSON.parse(res.data.actionList))
                        //        console.log(JSON.parse(res.data.actionList))
                        //    })

                    });
            },

            Login(data, store) {
                return service.post('/api/Account/Login', data)
                    .then(res => {
                        store.state.isAuthenticated = true;
                        store.state.token = res.token;
                        store.state.claims = jwt_decode(res.token);


                        
                        //this.$store.state.claims = JSON.parse(response.data.item2);
                        //this.GetUserInfo(store);
                    });
            }
        },
    /*login() {
        this.$refs.loginForm.validate().then(success => {
            if (success) {
                useJwt.login({
                    UserName: this.UserName,
                    Password: this.Password,
                })
                    .then(response => {
                        const { userData } = response.data
                        useJwt.setToken(response.data.accessToken)
                        useJwt.setRefreshToken(response.data.refreshToken)
                        localStorage.setItem('userData', JSON.stringify(userData))
                        this.$ability.update(userData.ability)

                        // ? This is just for demo purpose as well.
                        // ? Because we are showing eCommerce app's cart items count in navbar
                        //this.$store.commit('app-ecommerce/UPDATE_CART_ITEMS_COUNT', userData.extras.eCommerceCartItemsCount)

                        // ? This is just for demo purpose. Don't think CASL is role based in this case, we used role in if condition just for ease
                        this.$router.replace(getHomeRouteForLoggedInUser(userData.role))
                            .then(() => {
                                this.$toast({
                                    component: ToastificationContent,
                                    position: 'top-right',
                                    props: {
                                        title: `Welcome ${userData.fullName || userData.username}`,
                                        icon: 'CoffeeIcon',
                                        variant: 'success',
                                        text: `You have successfully logged in as ${userData.role}. Now you can start to explore!`,
                                    },
                                })
                            })
                    })
                    .catch(error => {
                        this.$refs.loginForm.setErrors(error.response.data.error)
                    })
            }
        })
    },*/



    /*this.$refs.loginValidation.validate().then(success => {
        if (success) {
            this.$toast({
            component: ToastificationContent,
            props: {
                title: 'Form Submitted',
                icon: 'EditIcon',
                variant: 'success',
            },
            })
        }
        })
    },*/
        
}
</script>

<style lang="scss">
@import '@core/scss/vue/pages/page-auth.scss';
